import React, { useState } from "react";
import Rating from "../Rating";
import styles from "../../../index.module.css";
import cx from "classnames";
import moment from "moment";

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
import keyword_extractor from "keyword-extractor";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import { Chevron, Eye } from "../../svg";

const VideoSliderComponent = (props) => {
  const { brandName, videoReviews } = props;
  const [expand, setExpand] = useState();

  const handleClick = (idx) => {
    expand !== idx ? setExpand(idx) : setExpand();
  };
  //To take review as argument and return the keyphrases
  const keyPhrase = (text) => {
    const extraction_result = keyword_extractor
      .extract(text, {
        language: "english",
        remove_digits: true,
        return_changed_case: true,
        remove_duplicates: true,
        return_chained_words: true,
        return_max_ngrams: true,
      })
      .splice(0, 4);
    return extraction_result;
  };

  // console.log("haha", props.videodata);

  return (
    <div className={styles.wrapper}>
      <div className={styles.reviews__gallery__body}>
        <Swiper
          spaceBetween={10}
          slidesPerView={"auto"}
          navigation
          modules={[Navigation]}
        >
          {videoReviews &&
            videoReviews.map((item, idx) => {
              return (
                item.video_name && (
                  <SwiperSlide key={idx}>
                    <div className={styles.reviews__gallery__item}>
                      <div className={styles.video__frame}>
                        <iframe
                          width='370'
                          height='650'
                          src={item.video_name}
                          title={item.video_name}
                          allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
                        ></iframe>
                        <div className={styles.bottom__hover}>
                          <p className='text-center'>Hover/Hold to view info</p>
                          <div className={styles.bottom__frame}>
                            <p>
                              <strong>{item.review_type}</strong>
                            </p>
                            <div
                              className={cx(
                                styles.rating__wrapper,
                                "d-flex align-items-center"
                              )}
                            >
                              <Rating rating={item.rating} />
                              <p className={styles.date}>
                                {moment(item.created)
                                  .utc()
                                  .format("DD/MM/YYYY")}
                              </p>
                              <div
                                className={cx(
                                  styles.views,
                                  "d-flex gap-2 align-items-center "
                                )}
                              >
                                <Eye />
                                <p>{item.view_count}</p>
                              </div>
                            </div>
                            <div className={styles.brand__info}>
                              <strong className={styles.ksname}>
                                {brandName && brandName}
                              </strong>
                              <div
                                onClick={() => handleClick(idx)}
                                className='d-flex justify-content-between align-items-baseline'
                              >
                                <div>
                                  <strong>Unbiased Review</strong> by:{" "}
                                  <strong className={styles.ksname}>
                                    {item.ks_customers.first_name}{" "}
                                    {item.ks_customers.last_name}
                                  </strong>
                                </div>
                                <span
                                  className={cx(
                                    "arrow-down",
                                    expand ? "up" : null
                                  )}
                                >
                                  <Chevron />
                                </span>
                              </div>
                            </div>
                            <div
                              hidden={expand !== idx}
                              className={styles.video__review}
                            >
                              {item.review}
                            </div>

                            <div className={styles.video__tags}>
                              {keyPhrase(item.review).map((item, idx) => {
                                return (
                                  <p key={idx} className={styles.tag__item}>
                                    {item}
                                  </p>
                                );
                              })}
                            </div>
                            <div className={styles.bottom__bar}>
                              <p>
                                Review Featured on:{" "}
                                <a
                                  target='_blank'
                                  href='https://www.nykaa.com'
                                  rel='noreferrer'
                                >
                                  Nykaa
                                </a>
                              </p>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </SwiperSlide>
                )
              );
            })}
        </Swiper>
      </div>
    </div>
  );
};

export default VideoSliderComponent;
