import React from "react";
import cx from "classnames";
import styles from "../../../index.module.css";
import { Cross } from "../../svg";

const FilterComponent = () => {
  const [activeTag, setActiveTag] = React.useState([]);

  const handleClick = (id) => {
    activeTag.includes(id)
      ? setActiveTag([...activeTag.filter((item) => item !== id)])
      : setActiveTag((existingItems) => {
          return [...existingItems, id];
        });
  };
  // console.log('activeTag', activeTag);

  return (
    <div className={cx(styles.reviews__filter, " row my-5")}>
      <div className={cx(styles.filter, "col ")}>
        {[
          "Value for money",
          "Good quality",
          "Smooth",
          "Smudge-free",
          "Pigmented",
        ].map((item, idx) => {
          return (
            <button
              key={idx}
              onClick={() => handleClick(idx)}
              className={cx(
                styles.tag,
                activeTag.includes(idx) ? styles.active : ""
              )}
            >
              {item}
              {activeTag.includes(idx) ? <Cross /> : null}
            </button>
          );
        })}
      </div>
    </div>
  );
};

export default FilterComponent;
