import React, { useState, useEffect } from "react";
import axios from "axios";
import styles from "../../index.module.css";
import cx from "classnames";
import ReviewComponent from "../subComponents/ReviewComponent";
import VideoSliderComponent from "../subComponents/VideoSliderComponent";
import SortComponent from "../subComponents/SortComponent";
import ScoreComponent from "../subComponents/ScoreComponent";
import { ArrowDown } from "../svg";

const Home = () => {
  const [textdata, settextdata] = useState([]);
  const [videodata, setvideodata] = useState([]);
  const [sort, setSort] = useState("date");
  const [loading, setLoading] = useState(false);
  const [visible, setVisible] = useState(6);
  const showMoreWorks = () => {
    setVisible((prevValue) => prevValue + 6);
  };

  //Fetching data from api by using id attributes passed in the render div element
  const merchantId = document.getElementById("khooobsooorat-widget").dataset
    .merchantId;
  const merchantProductId = document.getElementById("khooobsooorat-widget")
    .dataset.merchantProductid;

  useEffect(() => {
    //Fetch text reviews
    const fetchTextReviews = async () => {
      setLoading(true);
      try {
        const response = await axios.get(
          `https://khooobsooorat-poc-1.herokuapp.com/review/getTextReviewsOfProduct/${merchantId}/${merchantProductId}?page=1&size=${visible}&filterBy=${sort}`
        );
        settextdata(response.data);
        setLoading(false);
      } catch (err) {
        settextdata(null);
        console.log(err);
      }
    };

    //Fetch video reviews
    const fetchVideoReviews = async () => {
      try {
        const response = await axios.get(
          `https://khooobsooorat-poc-1.herokuapp.com/review/getVideoReviewsOfProduct/${merchantId}/${merchantProductId}?page=1&size=${visible}&filterBy=${sort}`
        );
        setvideodata(response.data);
      } catch (err) {
        setvideodata(null);
        console.log(err);
      }
    };

    //Running both the api functions
    fetchVideoReviews();
    fetchTextReviews();
  }, [merchantId, merchantProductId, sort, visible]);

  //Destructuring text and video reviews data to pass in components
  const { averageRating, highlightedRatings, textReviews, totalTextReviews } =
    textdata;
  const { totalVideoReviews, brandName, videoReviews } = videodata;

  //Calculating total review data for score component
  const totalReviews = totalTextReviews + totalVideoReviews;

  // console.log('mydata', reviewData);
  // console.log("haha", averageRating);

  return (
    <div>
      <div className={styles.reviews}>
        <div className={styles.reviews__header}>
          <div className={styles.reviews__gallery}>
            <div className={styles.reviews__gallery__header}>
              <h4 className={styles.header}>
                Unbiased Reviews by{" "}
                <span className={styles.ksname}>Khooobsooorat</span>
              </h4>
            </div>
            <hr className='mb-0' />
            <ScoreComponent
              totalReviews={totalReviews}
              averageRating={averageRating}
              featureData={highlightedRatings}
            />
            {videoReviews && videoReviews.length !== 0 && (
              <hr className='mb-4' />
            )}
            <VideoSliderComponent
              brandName={brandName}
              videoReviews={videoReviews}
            />
          </div>
        </div>
        <hr />
        <div className={styles.reviews__body}>
          <SortComponent setSort={setSort} />
          <ReviewComponent reviewData={textReviews} />
        </div>
        <div className={styles.reviews__footer}>
          {loading ? (
            <div className='text-center'>
              <span
                className='spinner-border text-danger text-center'
                role='status'
                aria-hidden='true'
              />
            </div>
          ) : (
            <div className={styles.read__more}>
              {totalTextReviews && totalTextReviews <= visible ? (
                <span className={cx(styles.text, "me-2")}>No more reviews</span>
              ) : (
                <span
                  className={cx(styles.text, "me-2")}
                  onClick={showMoreWorks}
                >
                  Read more reviews <ArrowDown />
                </span>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Home;
