import React from "react";
import styles from "../../../index.module.css";
import cx from "classnames";
import { StarFull, StarFullEmpty, StarFullHalf } from "../../svg";

export default function Rating(props) {
  const { rating } = props;
  return (
    <div className={cx(styles.rating, " d-flex")}>
      {rating >= 1 ? (
        <StarFull />
      ) : rating >= 0.5 ? (
        <StarFullHalf />
      ) : (
        <StarFullEmpty />
      )}

      {rating >= 2 ? (
        <StarFull />
      ) : rating >= 1.5 ? (
        <StarFullHalf />
      ) : (
        <StarFullEmpty />
      )}

      {rating >= 3 ? (
        <StarFull />
      ) : rating >= 2.5 ? (
        <StarFullHalf />
      ) : (
        <StarFullEmpty />
      )}

      {rating >= 4 ? (
        <StarFull />
      ) : rating >= 3.5 ? (
        <StarFullHalf />
      ) : (
        <StarFullEmpty />
      )}

      {rating >= 5 ? (
        <StarFull />
      ) : rating >= 4.5 ? (
        <StarFullHalf />
      ) : (
        <StarFullEmpty />
      )}
    </div>
  );
}
